@import '~@src/assets/styles/COMMON_COLOR_CONFIG';
.main {
  .itemText {
    font-size: 1.2rem!important;
    cursor: pointer;
  }
  .smile {
    background: #87d068;
  }
  .meh {
    background: #F5A623;
  }
  .frown {
    background: #00aeae;
  }
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 12px;
    line-height: 34px;
  }
  .pageBtn {
    border-color: transparent;
    box-shadow: none;
    color: $themeColor;
    font-size: 1.3rem;
  }
}
