@import '~@src/assets/styles/COMMON_COLOR_CONFIG';
.App {
  font-family: LingWai SC, sans-serif;
  .header {
    min-height: 10vh;
    padding: 10px 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    color: white;
    background-color: $themeColor;
    font-family: Hanzipen TC, serif;
    .title {
    }
    .avatar {
      background: #ffbf00;
    }
  }
  .nav {
    //font-family: sans-serif;
    width: 60%;
    margin: 10px auto;
    border-bottom: 1px $themeColor solid;
    font-size: 1.3rem;
    padding: 0.5rem 0;
    .link {
      color: $secondColor;
      margin-right: 1rem;
    }
    .link:hover {
      color: $themeColor;
    }
    .activeLink {
      color: $themeColor;
    }
  }
  .body{
    width: 60%;
    margin: 10px auto;
    min-height: calc(90vh - 215px);
  }
  @media only screen and (max-width: 768px) {
    .nav, .body {
      width: 90%;
    }
  }
  .footer {
    text-align: center;
    font-size: 12px;
    font-weight: lighter;
    line-height: 1.45em;
    font-family: Exo,sans-serif;
    color: #666665;
    margin: 80px 0 20px;
    .copyright {
      font-size: 1.1rem;
      font-family: LingWai SC, sans-serif;
    }
    a {
      color: #666665;
    }
  }
}
::selection {
  background: $secondColor;
}