.form {
  .item {
    :global {
      .ant-form-item-required {
        font-size: 1.2rem;
        font-weight: bold;
        color: rgba(0, 0, 0, 0.7);
      }
    }
  }
  .input {
    font-size: 1.2rem;
  }
  .radio {
    font-size: 1.2rem;
    span {
      vertical-align: middle;
    }
  }
  .braftEditor {
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    :global {
      .bf-controlbar {
        font-family: sans-serif;
      }
      .bf-content {
        height: 350px;
        font-size: 1.3rem;
        //color: rgba(0, 0, 0, 0.7);
        letter-spacing: 0.12rem;
      }
    }
  }
}
